.background {
  position: relative;
  min-height: 100px;
  background: #171819;
  padding: 30px;

  &:before {
    content: '';
    width: 100%;
    height: 4px;
    background-color: #171819;
    position: absolute;
    top: -2px;
    left: 0;
  }
}

.login {
  display: flex;
  background: #171819;
  align-items: center;
  justify-content: center;
  margin: 0 auto;

  @media (max-width: 768px) {
    flex-direction: column;
  }

  .text {
    font-weight: 700;
    color: #fff;
    line-height: 26px;
    letter-spacing: 0.8px;
    padding: 0 70px 15px;
    @media (max-width: 768px) {
      padding: 0  0 15px 0;
    }
  }
  .field {
    display: flex;
    justify-content: space-between;

    .email {
      border: 1px solid #737373;
      border-right: none;
      color: #48555B;
      padding: 11px 0 11px 16px;
      width: 259px;

      @media (max-width: 768px) {
        width: 220px;
      }
    }
  }
}