.banner {
  text-align: center;
  padding: 40px 0 65px 0;
  background: url('image/banner.jpg') no-repeat center;
  background-size: 100% 100%;

  @media (max-width: 1440px) {
    background-size: cover;
  }
  @media (max-width: 1024px) {
    padding: 15px 0 35px 0;
    margin-bottom: 10px;
  }

  .logo {

  }

  .content {
    min-height: 768px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    @media (max-width: 1024px) {
      min-height: 568px;
    }
    @media (max-width: 768px) {
      min-height: 460px;
    }
  }

  .title {
    font-family: "Bebas Neue";
    font-size: 60px;
    letter-spacing: 5px;
    line-height: 70px;
    text-transform: uppercase;

    @media (max-width: 768px) {
      font-size: 44px;
      line-height: 44px;
    }
  }

  .text {
    font-size: 16px;
    line-height: 1.2;
  }

  .subtext {
    font-size: 11px;
    line-height: 12px;
    letter-spacing: 3px;
  }
}
